  font-weight: 800;
  line-height: 3rem;
}

p {
  line-height: 1.6rem;
}

a {
  text-decoration: none;
}

html {
  scroll-behavior: smooth;
}

button {
  border: none;
  padding: 12px 18px;
  background-color: #84FFC5;
  background-color: var(--accent-color);
  font-size: .9rem;
  color: black;
}


