.home {
  height: 100vh;
  width: 100%;
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 250px auto 200px;
      grid-template-columns: 250px auto 200px;
}

.nav {
  height: 100vh;
  overflow: hidden;
  border-right: 2px solid whitesmoke;
  display: flex;
  flex-direction: column;
}

.nav img {
  width: 100%;
}

.nav header {
  padding: 20px;
}

.nav header img {
  width: 80px;
}

.nav .profile-preview {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 10px;
  margin-top: auto;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  width: 100%;
  display: flex;
  align-items: center;
  border-top: 1px solid whitesmoke;
