  width: 44px;
  height: 20px;
  background-color: #e9e9e9;
  display: flex;
  align-items: center;
  transition: background 100ms ease;
}

.switch1 .switch-head {
  width: 20px;
  height: 20px;
  background-color: #b8b8b8;
  transition: background 100ms ease, margin 400ms ease, width 400ms ease, -webkit-filter 100ms ease;
  transition: background 100ms ease, filter 100ms ease, margin 400ms ease, width 400ms ease;
  transition: background 100ms ease, filter 100ms ease, margin 400ms ease, width 400ms ease, -webkit-filter 100ms ease;
}

.switch1 .track-on {
